<template lang="html">
  <div class="row" data-vv-scope="form">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <form-card
        v-for="group of centerGroups"
        :group="group"
        v-model="form"
        @updateHandle="updateHandle"
      ></form-card>
      <custom-field
        v-if="customField.length > 0"
        :group="customField"
        :value="custom_field"
        v-model="custom_field"
      ></custom-field>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <form-card
        v-for="group of sideGroups"
        hasTop="true"
        :group="group"
        v-model="form"
      >
      </form-card>
      <div class="card" v-if="listView">
        <div class="card-header">
          <h6 class="title">Giao diện</h6>
        </div>
        <div class="card-body form-card">
          <div class="row">
            <div class="col-sm-12">
              <my-select
                class="full-width"
                :attribute="listView"
                placeholder="Giao diện"
                v-model="form.template"
              >
              </my-select>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hủy</el-button>
        <el-button type="primary" @click="save">Lưu lại</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import FormCard from 'src/components/UIComponents/FormCard.vue'
import CustomField from 'src/components/UIComponents/CustomField.vue'
import { Message, Input, Button } from 'element-ui'
import MySelect from 'src/components/UIComponents/Select'
import dataFrom from './article-form'

let _form = {}, _custom_field = {}, firstGroups, secondGroups;

export default {
  components: {
    FormCard,
    CustomField,
    ElInput: Input,
    ElButton: Button,
    Message,
    MySelect
  },
  beforeCreate() {
    firstGroups = dataFrom[0].groups;
    secondGroups = dataFrom[1].groups;

    firstGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    secondGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    });
    _form['template'] = '';

    this.$store.dispatch('getCustomField', {type: 'article'});
    this.$store.dispatch('getListThemeView', 'article');
  },
  data () {
    return {
      centerGroups: firstGroups,
      sideGroups: secondGroups,
      form: _form,
      custom_field: _custom_field,
      show_fields: []
    }
  },
  computed: {
    customField: {
      get() {
        return this.$util.filterByHandle(this.$store.state.customField, this.show_fields);
      },
      set(value) {}
    },
    listView() {
      let self = this;
      let listTheme = self.$store.state.listThemeView;
      if (listTheme.length) {
        listTheme.unshift({
          title: 'Mặc định',
          value: ''
        })
        return {
          options: listTheme
        };
      }
      return '';
    }
  },
  mounted() {
    this.$store.dispatch('setPageTitle', 'Tạo bài viết');
    this.$store.dispatch('setCurrentActions', [{
      label: 'Lưu lại',
      type: 'primary',
      icon: '',
      callback: this.save
    }])
    this.getContext();
    this.form.status = 'active';
    this.form['seo.meta_robots'] = 'index, follow';
  },

  methods: {
    cancel() {
      this.$router.push({name: 'AllArticles'});
    },
    updateHandle(){
      let self = this;
      self.form.handle = self.form.title;
      let data = {
        handle: self.$util.createHandle(self.form.title),
        lang: 'vi'
      }
      self.$store.dispatch('checkHandle', data).then(result => {
        self.form.handle = result;
      });
    },
    save() {
      let self = this;
      this.$validator.validateAll().then(function (result) {
        if (result){
          let blogs = [];
          if (self.form.objBlog) {
            for (var item in self.form.objBlog) {
              let blog = self.form.objBlog[item];
              blogs.push(blog.id);
            }
          }
          self.form.blogs = blogs;
          self.$store.dispatch('storeArticle', self.form).then(res => {
            self.$store.dispatch('storeCustomField', self.$util.parseDataCustomfield(self.customField, self.custom_field, 'article', res.id));

            Message({
              message: 'Tạo mới thành công',
              type: 'success'
            });

            self.$router.push({name: 'AllArticles'})

          }, error => {
            Message({
              message: res.message,
              type: 'danger'
            });
          });
        }
      })
    },
    getContext() {
      let ctx = this.$util.getContext(this.form, {module: 'articles'});
      this.$store.state.ctx = ctx;
      return ctx;
    }
  },
  watch: {
    'form.title'(newVal, oldVal) {
      let self = this;
      self.form['seo.meta_title'] = newVal;
      self.form.handle = newVal;
      let data = {
        handle: self.$util.createHandle(newVal),
        lang: 'vi'
      }
      self.$store.dispatch('checkHandle', data).then(result => {
        self.form.handle = result;
      });
    },
    'form.description'(newVal, oldVal) {
      this.form['seo.meta_description'] = newVal;
    },
    'form': {
      handler(newVal, oldVal) {
        let ctx = this.getContext();
        let output = window.__FUNC.CustomField(ctx);
        this.show_fields = output;
      },
      deep: true
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
    this.$store.state.listThemeView = [];
    this.$store.state.customField = [];
  }
}
</script>
